angular.module('ecom', ['ui.router'])
    .config(function($urlRouterProvider, $stateProvider){
      $stateProvider
          .state('home', {
              url: '/home',
              template: '<home></home>',
              component: 'home'
          })
          .state('login', {
              url: '/login',
              template: '<login></login>',
              component: 'login'
          })
          .state('signup', {
              url: '/signup',
              template: '<sign-up></sign-up>',
              component: 'signUp'
          });
      $urlRouterProvider.otherwise('/');
    })

