angular.module('ecom').service('mainService', function ($http) {
    var serverUrl = 'http://localhost:3060'
    this.getProducts = () => {
        return $http({
            method: 'GET',
            url: serverUrl + '/api/products'
        })
    };
    this.login = () => {
        return $http({
            method: 'POST',
            url: serverUrl + '/api/login'
        })
    };
    this.signUp = () => {
        return $http({
            method: 'POST',
            url: serverUrl + '/api/newuser'
        })
    }
})
